<template>
  <div>
    <!--    面包屑导航-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
<!--    卡片视图-->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showCateDialog">添加分类</el-button>
        </el-col>
      </el-row>
<!--      表格-->
    <tree-table :data="cateList" :columns="columns" :selection-type="false" :expand-type="false" show-index index-text="#" border :show-row-hover="false" class="tree_table">
<!--      是否有效-->
      <template slot="isValid" slot-scope="scope">
        <i class="el-icon-success" v-if="scope.row.cat_deleted===false"></i>
        <i class="el-icon-error" v-else></i>
      </template>
<!--      级别-->
      <template slot="level" slot-scope="scope">
        <el-tag size="mini" v-if="scope.row.cat_level===0">一级</el-tag>
        <el-tag type="success" size="mini" v-else-if="scope.row.cat_level===1">二级</el-tag>
        <el-tag type="warning" size="mini" v-else>三级</el-tag>
      </template>
<!--      操作-->
      <template slot="option" slot-scope="scope">
        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditCateDialog(scope.row.cat_id)">编辑</el-button>
        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeCateById(scope.row.cat_id)">删除</el-button>
      </template>
    </tree-table>
<!--      分页区域-->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1, 3, 5, 10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>
    <!--    添加分类的对话框-->
    <el-dialog title="添加分类" :visible.sync="dialogVisible" width="40%" @close="addDialogClosed">
      <!--      对话框内容主题区域-->
      <el-form :model="cateForm" :rules="cateFormRules" ref="cateFormRef" label-width="70px">
        <el-form-item label="分类名称" prop="cat_name" label-width="80px">
          <el-input v-model="cateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类" label-width="80px">
          <el-cascader
              v-model="selectedKeys"
              :options="parentCateList"
              :props="cascaderProps"
              @change="parentCateChanged"
              clearable
              change-on-select
          >
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>
<!--    编辑分类的对话框-->
    <el-dialog title="修改分类" :visible.sync="editCateDialogVisible" width="30%">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="分类名称" prop="cat_name" label-width="80px">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editCateInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Categories",
  data(){
    return{
      //查询条件
      queryInfo:{
        type:3,
        pagenum:1,
        pagesize:5
      },
      // 商品分类数据列表
      cateList:[],
      // 总数据条数
      total:0,
      //为table指定列的定义
      columns:[
        {
          label:'分类名称',
          prop:'cat_name'
        },
        {
          label:'是否有效',
          // 将当前列定义为模板列
          type:'template',
          // 表示当前列模板名称
          template:'isValid'
        },
        {
          label:'排序',
          // 将当前列定义为模板列
          type:'template',
          // 表示当前列模板名称
          template:'level'
        },
        {
          label:'操作',
          // 将当前列定义为模板列
          type:'template',
          // 表示当前列模板名称
          template:'option'
        }
      ],
      dialogVisible:false,
      cateForm: {
        cat_name:'',
        cat_pid:0,
        cat_level:0
      },
      cateFormRules:{
        cat_name:[
          {required: true, message: '请输入分类名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur'}
        ]
      },
      // 父级分类列表
      parentCateList:[],
      // 指定级联选择器的配置对象
      cascaderProps:{
        value:'cat_id',
        label:'cat_name',
        children:'children'
      },
      // 选中的父级分类id数组
      selectedKeys:[],
      //编辑分类对话框的显示隐藏
      editCateDialogVisible:false,
      editForm:{
        cat_name:'',
      },
      editFormRules:{
        cat_name:[
          {required: true, message: '请输入分类名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur'}
        ]
      }
    }
  },
  created() {
      this.getCateList()
  },
  methods:{
    //获取商品分类数据列表
    async getCateList(){
      const {data:res}=await this.$http.get('categories',{
        params:this.queryInfo
      })
      if (res.meta.status!==200){
        return this.$message.error('获取商品数据失败!')
      }
      this.cateList=res.data.result
      this.total=res.data.total
    },
    // 监听pagesize改变
    handleSizeChange(newsize){
      this.queryInfo.pagesize=newsize
      this.getCateList()
    },
    // 监听pagenum改变
    handleCurrentChange(newPage){
      this.queryInfo.pagenum=newPage
      this.getCateList()
    },
    showCateDialog(){
      this.getParentCateList()
      this.dialogVisible=true
    },
    addDialogClosed(){
      this.$refs.cateFormRef.resetFields();
      this.selectedKeys=[];
      this.cateForm.cat_level=0;
      this.cateForm.cat_pid=0;
    },
    async getParentCateList(){
      const {data:res}=await this.$http.get('categories',{
        params:{
          type:2
        }
      })
      if (res.meta.status!==200){
        return this.$message.error('获取父级分类数据失败')
      }
      this.parentCateList=res.data
      console.log(this.parentCateList)
    },
    // 选这项发生变化触发
    parentCateChanged(){
      // 如果 selectedKeys 数组中的length大于0，证明选中的分类为父级
      if (this.selectedKeys.length>0){
        this.cateForm.cat_pid=this.selectedKeys[this.selectedKeys.length-1]
        this.cateForm.cat_level=this.selectedKeys.length
        return
      }else{
        this.cateForm.cat_pid=0
        this.cateForm.cat_level=0
      }
    },
    addCate(){
      console.log(this.cateForm)
      this.$refs.cateFormRef.validate(async valid=>{
        if (!valid)
          return
        const {data:res}=await this.$http.post('categories',this.cateForm)
        if (res.meta.status!==201){
          return this.$message.error('添加分类失败')
        }
        //关闭对话框
        this.dialogVisible=false
        //刷新数据列表
        this.getCateList();
        //提示更新角色信息成功
        this.$message.success('添加分类成功')
      })
    },
    async showEditCateDialog(id){
      const {data:res}=await this.$http.get('categories/'+id);
      if(res.meta.status!==200){
        return this.$message.error('查询分类信息失败')
      }
      this.editForm=res.data
      console.log(this.editForm)
      this.editCateDialogVisible=true
    },
    editCateInfo(){
      this.$refs.editFormRef.validate(async valid=>{
        if (!valid)
          return
        const {data:res}=await this.$http.put('categories/'+this.editForm.cat_id,{
          cat_name:this.editForm.cat_name
        })
        if (res.meta.status!==200){
          return this.$message.error('编辑分类失败')
        }
        //关闭对话框
        this.editCateDialogVisible=false
        //刷新数据列表
        this.getCateList();
        //提示更新角色信息成功
        this.$message.success('编辑分类成功')
      })
    },
    async removeCateById(id){
      const res=await this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err=>err)
      if (res!=='confirm'){
        return this.$message.info('已取消删除')
      }
      const {data:response}=await this.$http.delete('categories/'+id)
      if (response.meta.status!==200){
        return this.$message.error('分类删除失败')
      }
      this.$message.success('该分类已删除')
      this.getCateList()
    }
  }
}
</script>

<style lang="less" scoped>
  .el-icon-success{
    color: lightgreen;
  }
  .el-icon-error{
    color: red;
  }
  .tree_table{
    margin: 10px 0;
  }
  .el-cascader{
    width: 100%;
  }
</style>